.a012b6d4 {
  left: 0;
  top: 0;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 500ms ease;
}

.fc7060d8 {
  position: absolute;
  top: -130px;
  left: -30px;
  width: 60px;
  height: 130px;
  transform-style: preserve-3d;
}
._3003b329 {
  transform-style: preserve-3d;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  border: solid 4px #2a9d8f;
  /*box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);*/

  transition: border-color 300ms ease;

  position: absolute;
  top: 0;
  left: 0;
}

.da971179,
._5a42ea86 {
  transform-style: preserve-3d;
  width: 4px;
  position: absolute;
  top: 60px;
  bottom: 0;
  left: calc(50% - 4px);
  background-color: #2a9d8f;
  transition: background-color 300ms ease;
}
._5a42ea86 {
  transform: rotateY(90deg);
}

.f857ae82 .da971179,
.f857ae82 ._5a42ea86 {
  background-color: #fff;
}
.f857ae82 ._3003b329 {
  border-color: #fff;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.6);
}
